
 const Theme = {
    LIGHT: 'light-theme',
    DARK: 'dark-theme',
  };

export function onChangeThemeToBlack(e) {
 if (e.target.nodeName === 'INPUT') {
document.body.classList.toggle("Theme.DARK");
 }
}

